<template>
  <div id="type8-item">
    <header>
      <van-nav-bar
        title="读诗人"
        class="nav-content"
        @click-left="clickLeft"
        @click-right="clickRight"
      >
        <template #left>
          <img src="../../assets/imgs/images/pre.png" />
        </template>
        <template #right>
          <img src="../../assets/imgs/images/share.png" />
          <van-share-sheet
            v-model="showShare"
            :options="options"
            get-container="#type8-item"
          />
        </template>
      </van-nav-bar>
    </header>
    <div class="reading-book">
      <div class="search-item">
        <SearchItem
          class="search-item-com"
          :pic="authorInfo.img_new"
          :title="authorInfo.g_name"
          :readingBook="true"
          :SecondTitle="authorInfo.description"
          :canClick="false"
        ></SearchItem>
      </div>
      <div class="old-reader">
        <h3>往期[为您读诗]</h3>
        <div class="search-item">
          <SearchItem
            v-for="item in works"
            :key="item.id"
            :id="item.id"
            :pic="item.str_img_url"
            :SecondTitle="item.view_count"
            :title="item.str_program_title"
            :readingperson="true"
            :type="4"
          ></SearchItem>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem.vue";
export default {
  name: "Type8Item",
  components: { SearchItem },
  data() {
    return {
      showShare: false,
      authorInfo: {},
      works: [],
      options: [
        { name: "微信", icon: "wechat" },
        { name: "朋友圈", icon: "wechat-moments" },
        { name: "QQ", icon: "qq" },
      ],
    };
  },
  created() {
  this.$nextTick(()=>{
      this.getData();
  })
  },
  methods: {
    clickLeft() {
      this.$router.go(-1);
    },
    clickRight() {
      this.showShare = true;
    },
    async getData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getGuestInfo",
        data: {
          gid: this.$route.query.id,
          page: 1,
          limit: 10,
          str: "",
        },
      });
      this.authorInfo = data.data.data;
      let data1 = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getGuestProgram",
        data: {
          gid: this.$route.query.id,
          page: 1,
          limit: 10,
          str: "",
        },
      });
      this.works = data1.data.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#type8-item {
  header {
    position: sticky;
    z-index: 999;
    top: 0;
    left: 0;
    height: 50px;
    padding: 0 20px;
    .nav-content {
      .van-nav-bar__left {
        img {
          width: 10px;
        }
      }
      .van-nav-bar__right {
        img {
          width: 20px;
        }
      }
    }
  }
  .reading-book {
    margin: 15px;
    .search-item-com {
      padding: 10px;
      border-radius: 10px;
      box-shadow: 0px 0px 8px var(--theme-gray);
    }
    .old-reader {
      h3 {
        margin: 20px 15px;
        font-family: fzqk;
        font-size: 18px;
      }
    }
  }
}
</style>